import {Injectable} from '@angular/core';
import {Router} from '@angular/router';
import {HttpClient} from '@angular/common/http';
import {User} from '../_models/user';


@Injectable({
  providedIn: 'root'
})
export class AuthService {
  constructor(private http: HttpClient, public router: Router) {
  }

  public currentUser: User;
  private readonly token: string = 'token';
  private loggedFirstName: string;
  private email: string;

  logIn() {
    localStorage.setItem('isLoggedIn', 'true');
  }

  logOut() {
    localStorage.setItem('isLoggedIn', 'false');
  }

  removeLastName() {
    localStorage.removeItem('lastname');
  }

  removeFirstName() {
    localStorage.removeItem('username');
  }

  removeToken() {
    localStorage.removeItem(this.token);
  }

  public login(email, password) {
    return this.http.post<any>('http://127.0.0.1:1234/token/', {email, password})
      .subscribe(
        data => {
          this.doLoginFirstName(data.first_name, data.last_name, data.access, data.is_teacher);
          localStorage.setItem('currentUser', data);
          this.currentUser = data;
        },
        error => {
          console.log(
            'Error: ', error.status, error.statusText);
          this.storeStatus(error.status);
        }
      );
  }

  private doLoginFirstName(first_name: string, last_name: string, token: string, isTeacher: string) {

    this.logIn();
    this.router.navigate(['/dashboard']);

    this.storeFirstName(first_name);
    this.storeLastName(last_name);
    this.storeTokens(token);
    this.storeIsTeacher(isTeacher);

    this.router.navigate(['/dashboard']);
  }

  logout(): void {
    this.logOut();
    localStorage.setItem('isLoggedIn', 'false');
    this.loggedFirstName = null;
    this.removeFirstName();
    this.removeLastName();

    this.removeToken();
    localStorage.setItem('i', '0');
    localStorage.setItem('j', '0');
    console.log('FirstName logged out');
  }

  private storeStatus(status: string) {
    localStorage.setItem('status', status);
  }

  private storeIsTeacher(isTeacher: string) {
    localStorage.setItem('isTeacher', isTeacher);
  }


  private storeFirstName(first_name: string) {
    localStorage.setItem('user', first_name);
  }


  private storeLastName(last_name: string) {
    localStorage.setItem('lastname', last_name);
  }

  private storeTokens(token: string) {
    localStorage.setItem(this.token, token);
  }

  getLoggedUser() {
    return localStorage.getItem('user');
  }

  getLastName() {
    return localStorage.getItem('lastname');
  }

  getJwtToken() {
    return localStorage.getItem(this.token);
  }

  getIsTeacher() {
    return localStorage.getItem('isTeacher');
  }

  getStatus() {
    return localStorage.getItem('status');
  }

}
