import {Component, Input, OnInit} from '@angular/core';
import {AuthService} from '../_services/auth.service';
import {Course} from '../_models/course';
import {EnrollmentService} from '../_services/enrollment.service';
import {HttpClient} from '@angular/common/http';
import {Router} from '@angular/router';


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  courseModel = new Course('', '');
  teacher = this.authService.getIsTeacher();
  id: string;
  i: number = Number(localStorage.getItem('i'));

  // tslint:disable-next-line:ban-types
  courses: Object;
  author = this.authService.getLoggedUser() + ' ' + this.authService.getLastName();
  over: boolean;
  selectedCourse;

  constructor(private authService: AuthService, private enrollmentService: EnrollmentService, private http: HttpClient, private router: Router) {
  }

  ngOnInit() {
    this.id = this.authService.getLoggedUser();

    this.http.get('http://127.0.0.1:1234/api/courses/teacher_courses/').subscribe(
      data => {
        console.log(data),
          this.courses = data;

      },
      error => (console.log(error))
    );

    console.log('User logged in');
    if (this.i === 0) {
      localStorage.setItem('i', '1');
      location.reload();
    }
  }

  create() {
    console.log(this.courseModel);
    this.enrollmentService.enrollCourse(this.courseModel);
  }

  onSelect(course: Course): void {
    this.selectedCourse = course;
    localStorage.setItem('title', this.selectedCourse.title);
    localStorage.setItem('body', this.selectedCourse.body);
    localStorage.setItem('author', this.selectedCourse.author);
    this.router.navigate(['course']);
  }

}

