import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {User} from '../_models/user';
import {Router} from '@angular/router';
import {Course} from '../_models/course';
import {AuthService} from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class EnrollmentService {
  urlRegister = 'http://127.0.0.1:1234/auth/users/';
  urlCreateCourse = 'http://127.0.0.1:1234/api/courses/list/';


  constructor(private http: HttpClient, private router: Router, private authService: AuthService) {
  }

  enrollUser(user: User) {
    return this.http.post(this.urlRegister, user).subscribe(
      data => {
        console.log('Success', data);
        this.router.navigate(['home']);
      },
      error => console.log('Error:', error.status, error.statusText)
    );
  }

  enrollCourse(course: Course) {
    // tslint:disable-next-line:ban-types
    const requestOptions: Object = {
      headers: new HttpHeaders().append('Authorization', 'Bearer' + this.authService.getJwtToken()),
      responseType: 'text'
    };
    this.http.post(this.urlCreateCourse, course, requestOptions)
      .subscribe(
        data => {
          console.log('Success', data);
          localStorage.setItem('created', 'true');
        },
        error => console.log('Error:', error));
  }
}
