import {Component, OnInit} from '@angular/core';
import {AuthService} from '../_services/auth.service';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {AuthGuard} from '../_services/auth.guard';
import {Login} from '../_models/login';
import {log} from 'util';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  message: string;

  constructor(private authService: AuthService, private formBuilder: FormBuilder, private router: Router, private authGuard: AuthGuard) {
  }

  loginModel = new Login('', '');

  ngOnInit(): void {
    this.loginForm = this.formBuilder.group({
      email: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  get f() {
    return this.loginForm.controls;
  }

  valid(): boolean {
    if (this.f.password.errors || this.f.email.errors) {
      return false;
    } else {
      return true;
    }
  }

  login() {
    const loginData = this.loginForm.value;
    if (this.valid()) {
      this.authService.login(loginData.email, loginData.password);
      if (this.authService.getStatus.toString() === '400') {
        this.message = 'Check your password and email';
        console.log('Credentials not valid');
      } else {
        console.log('Credentials valid');
      }
    } else {
      console.log('Credentials not valid');
      this.message = 'Check your password and email';
    }
  }
}
